  .search-box {
    background-color: #f7f7f7;
    padding: 16px;
    border-radius: 8px;
  }
  
  .search-bar-placeholder {
    font-size: 14px;
    color: #999;
    margin-bottom: 16px;
  }
  
  .search-section {
    margin-bottom: 32px;
  }
  
  .section-title {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 8px;
  }
  
  .search-row {
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 4px 0;
  }
  
  .rank {
    width: 20px;
    font-weight: bold;
    color: #245eff;
  }
  
  .keyword {
    flex: 1;
    font-size: 13px;
  }
  
  .change {
    font-size: 12px;
    color: red;
  }
  
  .timestamp {
    font-size: 10px;
    color: #888;
    margin-top: 8px;
    text-align: right;
  }
  