  }
  
  .box .image {
    position: absolute; /* 부모인 .overlap-group에 대해 상대적으로 위치 */
  top: 3px; /* 현재 위치 유지 */
  left: 2px; /* 현재 위치 유지 */
  width: 40px; /* 원래 이미지 크기 유지 (또는 둥근 박스 크기에 맞게 조절) */
  height: 39px; /* 원래 이미지 크기 유지 (또는 둥근 박스 크기에 맞게 조절) */
  border-radius: 50%; /* 완전한 원을 만들고 싶다면 50% */
                     /* 또는 border-radius: 10px; 등 해당 둥근 박스에 맞는 값 */
  object-fit: cover; /* 이미지가 잘리더라도 비율 유지하며 꽉 채움 */
  overflow: hidden; /* 이미지 내용이 둥근 테두리를 벗어나지 않도록 함 (안전 장치) */
  }
  
