/* src/components/SearchModal/RankedSearchList/RankedSearchList.module.css */
.listContainer {
    margin-bottom: 20px;
  }
  
  .titleHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .title {
    font-size: 16px;
    font-weight: bold;
    color: #333;
  }
  
  .timestamp {
    font-size: 12px;
    color: #888;
  }
  
  .columns {
    display: flex;
    gap: 20px; /* 컬럼 사이 간격 */
  }
  
  .column {
    flex: 1;
    min-width: 0; /* flex item이 너무 커지는 것을 방지 */
  }